@charset "UTF-8";
.index_area .pie_area>li {
	float:left;
	margin-right:20px;
	margin-bottom:20px}
@media only screen and (min-width:1600px) {
	.index_area .pie_area>li {
	width:calc( (100% - 80px) / 5)}
}
@media only screen and (min-width:1600px) {
	.index_area .pie_area>li:nth-child(5n) {
	margin-right:0}
}
@media only screen and (min-width:1600px) {
	.index_area .pie_area>li:nth-child(5n):nth-last-child(-n+6)~li {
	margin-bottom:0}
}
@media only screen and (min-width:1200px) and (max-width:1599px) {
	.index_area .pie_area>li {
	width:calc( (100% - 60px) / 4)}
}
@media only screen and (min-width:1200px) and (max-width:1599px) {
	.index_area .pie_area>li:nth-child(4n) {
	margin-right:0}
}
@media only screen and (min-width:1200px) and (max-width:1599px) {
	.index_area .pie_area>li:nth-child(4n):nth-last-child(-n+5)~li {
	margin-bottom:0}
}
@media only screen and (min-width:992px) and (max-width:1199px) {
	.index_area .pie_area>li {
	width:calc( (100% - 40px) / 3)}
}
@media only screen and (min-width:992px) and (max-width:1199px) {
	.index_area .pie_area>li:nth-child(3n) {
	margin-right:0}
}
@media only screen and (min-width:992px) and (max-width:1199px) {
	.index_area .pie_area>li:nth-child(3n):nth-last-child(-n+4)~li {
	margin-bottom:0}
}
@media only screen and (min-width:768px) and (max-width:991px) {
	.index_area .pie_area>li {
	width:calc( (100% - 20px) / 2)}
}
@media only screen and (min-width:768px) and (max-width:991px) {
	.index_area .pie_area>li:nth-child(2n) {
	margin-right:0}
}
@media only screen and (min-width:768px) and (max-width:991px) {
	.index_area .pie_area>li:nth-child(2n):nth-last-child(-n+3)~li {
	margin-bottom:0}
}
.index_area .pie_area .pie_inner h4,.index_area .pie_area .pie_inner h5,.index_area .pie_area .pie_inner h6 {
	letter-spacing:1px}
.index_area .pie_area .pie_inner .card-head {
    padding:15px}
.index_area .pie_area .pie_inner .card-head.green{
    background-color:#8fc31f;}
.index_area .pie_area .pie_inner .card-head.gray{
    background-color:#d3d6e7;}
.index_area .pie_area .pie_inner .card-head.orange{
    background-color:#ffb100;}
.index_area .pie_area .pie_inner .card-head h5,.index_area .pie_area .pie_inner .card-head h6 {
	color:#fff}
.index_area .pie_area .pie_inner .pie_data h6 {
	color:#808080}
.index_area .pie_area .pie_inner .pie_data.top h4 {
    margin-bottom:5px}
.index_area .pie_area .pie_inner .pie_data.top h4 .black {
    color:#2e355b;}
.index_area .pie_area .pie_inner .pie_data.top h4 .red {
    color:#eb0000;}
.index_area .pie_area .pie_inner .pie_data.top h4 .orange {
    color:#ffb100;}




.index_area .pie_area .pie_inner .pie_data.top span:nth-child(2) {
	border:.5px solid #ccc}
.index_area .pie_area .pie_inner .pie_data.bottom i {
	font-size:40px;
	margin-bottom:5px;
	-webkit-animation-direction:alternate;
	animation-direction:alternate}
.index_area .pie_area .pie_inner .pie_data.bottom .icon-arrow_upward {
	color:#3ed048;
	-webkit-animation:up 1s infinite;
	animation:up 1s infinite}
.index_area .pie_area .pie_inner .pie_data.bottom .icon-arrow_downward {
	color:#eb0000;
	-webkit-animation:down 1s infinite;
	animation:down 1s infinite}
.index_area .pie_area .pie_inner .pie_data.bottom .icon-remove {
	color:#3ed048}
.pie_chart {
	margin:40px auto 20px;
	position:relative}
@media only screen and (min-width:992px) {
	.pie_chart {
	width:200px;
	height:200px}
}
@media only screen and (min-width:768px) and (max-width:991px) {
	.pie_chart {
	width:250px;
	height:250px}
}
@media only screen and (min-width:768px) {
	.pie_chart {
	margin:30px auto 17px}
}
.pie_chart:after {
	content:'';
	position:absolute;
	left:50%;
	top:50%;
	-webkit-transform:translate(-50%,-50%);
	-ms-transform:translate(-50%,-50%);
	transform:translate(-50%,-50%);
	width:calc(100% - 40px);
	height:calc(100% - 40px);
	background:#fff;
	border-radius:inherit}
@media only screen and (min-width:992px) {
	.pie_chart:after {
	width:calc(100% - 40px);
	height:calc(100% - 40px)}
}
@media only screen and (min-width:768px) and (max-width:991px) {
	.pie_chart:after {
	width:calc(100% - 40px);
	height:calc(100% - 40px)}
}
.pie_chart .pie_info {
	position:relative;
	z-index:1}
.pie_chart .pie_info>h4 {
	color:#484067;
	font-size:45px;
	display:block;
	border-bottom:2px solid #8fc31f}
.pie_chart .pie_info>h6 {
	font-size:18px;
    line-height:16px;
    font-weight: 700;
    color:#5b1486}
.orangeButton {
    background-color: #f2f2f2;
    border: 2px solid #ffb100;
    cursor: pointer;
    padding: 0px;
}
.orangeButton:hover{
    box-shadow: 0px 1px 3px #ffb100;
}

.orangeButton H4{
    color:  #ffb100;
}
.my-2px { /*避免orangeButton的border寬把下方卡片擠掉*/
    margin-top: 2px;
    margin-bottom: 2px; 
}
