.tableOutDiv{
    border: 1px solid #ced4da;
    font-weight: 600;
    width: 100%;
    overflow: auto;
    height: 65vh;
}

.tableOutDiv .th_style{
    background-color: #2e355b;
    border: 0;
    cursor: pointer;
    color: rgba(255, 255, 255, 0.5);
}
.tableOutDiv .th_style span{
    color: #fff;
    position: relative;
}
.tableOutDiv .th_style span > span.th_sort:before{
    font-family: 'fcs';
    content: '\e90c';
    top: -8px;
    left: 3px;
    position: absolute;
    opacity: .3;
}
.tableOutDiv .th_style span > span.th_sort:after{
    font-family: 'fcs';
    content: '\e90a';
    bottom: -8px;
    left: 3px;
    position: absolute;
    opacity: .3;
}
.tableOutDiv .th_style span > span.th_sort.sort_asc:before{
    opacity: 1;
    color: #fff;
}
.tableOutDiv .th_style span > span.th_sort.sort_desc:after{
    opacity: 1;
    color: #fff;
}
.tableOutDiv table tr th,.tableOutDiv table tr td{
    text-align: center;
}
.textRed{
    color: #eb0000;
}
.textOrange{
    color: #ffb100;
}

